import { Injectable, Injector } from '@angular/core';
import {translate} from "./services/i18n";

@Injectable({
  providedIn: 'root'
})
export class WfBizNavigationService {

  constructor(private injector: Injector) { }

  getI18nValue(name: string) {
    if (!name) { return ''; }
    const defaultLang = localStorage.getItem('languageCode');
    const langData = defaultLang ? translate[defaultLang] : translate['zh-CHS'];
    let resultVal = '';
    if (name.indexOf('.') === -1) {
      resultVal = langData[name];
    } else {
      resultVal = name.split('.').reduce((obj, key) => {
        if (obj) {
          return obj[key];
        } else {
          return null;
        }
      }, langData);
    }
    return resultVal;
  }
}
