import { Pipe, PipeTransform } from '@angular/core';
import { WfBizNavigationService } from "../wf-biz-navigation.service";

@Pipe({ name: 'translate' })
export class TranslatePipe implements PipeTransform {

    constructor(
        private service: WfBizNavigationService
    ) { }

    transform(name: any, defaultVal = ''): any {
        const r = this.service.getI18nValue(name);
        return r ? r : defaultVal;
    }
}
