import { Component, OnInit, Injector, Output, EventEmitter, ViewChild, AfterViewInit, Input } from '@angular/core';
import { WFBizNavigation } from './wf-biz-navigation.entity';
import { BillcatTreeComponent } from '@gsp-bef/billcat-tree';
import { HttpService } from '@ecp-caf/caf-common';
import { TranslatePipe } from './services/translate.pipe';


@Component({
  selector: 'wf-biz-navigation',
  templateUrl: `./wf-biz-navigation.component.html`,
  styleUrls: ['./wf-biz-navigation.component.scss']
})
export class WfBizNavigationComponent implements OnInit, AfterViewInit {

  @Output()
  selectNodeEmitter = new EventEmitter<WFBizNavigation>();

  @Output()
  bizObject = new EventEmitter<WFBizNavigation>();

  @Output()
  bizCategory = new EventEmitter<WFBizNavigation>();

  @Input()
  tab: 'billCategory' | 'bizObject' = 'billCategory';

  @ViewChild(BillcatTreeComponent) billcat: BillcatTreeComponent;

  data = new Array();

  config = {
    expandLevel: 1
  };

  emptyData = new Array();
  emptyCols = [{
    field: 'code', width: 100, title: this.translate.transform('static.code'),
  }];

  constructor(
    private injector: Injector,
    private http: HttpService,
    private translate: TranslatePipe
  ) { }

  ngOnInit() {
    this.getBillcategoriesByExtend();
  }

  ngAfterViewInit() {
    // this.billcat.treeConfig.expandLevel = 1;
  }


  tabChange(args: any) {
    this.tab = args.nextId;
    // console.log({ tabchange: args });
  }

  boTreeSelectionChanged(data: any) {
    if (data.data && data.data.isDetail === '1') {
      const node: WFBizNavigation = {
        type: 'bo',
        nodeInfo: data
      };
      this.bizObject.emit(node);
      this.selectNodeEmitter.emit(node);
    }
  }


  billCategorySelectionChanged(data: any) {
    if (data && data.detail) {
      const node: WFBizNavigation = {
        type: 'bill',
        nodeInfo: data
      };
      this.bizCategory.emit(node);
      this.selectNodeEmitter.emit(node);
    }
  }

  private getBillcategoriesByExtend() {
    const url = '/api/runtime/bcc/v1.0/billcategory/billcategoriesByExtend?allextends=false&customconfig=processAssignManager';
    this.http.get(url).subscribe((re: any) => {
      if (re) {
        this.data = re;
      }
    });
  }
}
